import React, { useState } from 'react';
import {
  Container,
  Typography,
  Box,
  Grid,
  Card,
  CardMedia,
  CardContent,
  CardActions,
  Button,
  IconButton,
  Chip,
  Rating,
  useTheme,
  useMediaQuery,
  Alert,
} from '@mui/material';
import {
  Delete,
  ShoppingCart,
  LocalShipping,
  Star,
} from '@mui/icons-material';
import { Link as RouterLink } from 'react-router-dom';
import { products } from '../data/products';

// Mock favorites data - in a real app, this would come from a user's saved items
const initialFavorites = [1, 2, 4]; // Product IDs

const Favorites = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const [favorites, setFavorites] = useState(initialFavorites);

  const favoriteProducts = products.filter(product => favorites.includes(product.id));

  const removeFromFavorites = (productId) => {
    setFavorites(favorites.filter(id => id !== productId));
  };

  if (favorites.length === 0) {
    return (
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Box sx={{ textAlign: 'center', py: 8 }}>
          <Typography variant="h4" gutterBottom>
            No Favorites Yet
          </Typography>
          <Typography color="text.secondary" paragraph>
            Start adding items to your favorites while you shop
          </Typography>
          <Button
            component={RouterLink}
            to="/categories"
            variant="contained"
            color="primary"
            size="large"
            sx={{ mt: 2 }}
          >
            Browse Products
          </Button>
        </Box>
      </Container>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Box sx={{ mb: 4 }}>
        <Typography variant="h4" gutterBottom>
          My Favorites
        </Typography>
        <Alert severity="info" sx={{ mb: 2 }}>
          You have {favorites.length} items in your favorites
        </Alert>
      </Box>

      <Grid container spacing={3}>
        {favoriteProducts.map((product) => (
          <Grid item key={product.id} xs={12} sm={6} md={4}>
            <Card
              sx={{
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                position: 'relative',
                transition: 'all 0.3s ease',
                '&:hover': {
                  transform: 'translateY(-4px)',
                  boxShadow: '0 12px 24px rgba(0,0,0,0.1)',
                },
              }}
            >
              {product.bestseller && (
                <Chip
                  icon={<Star sx={{ fontSize: 16 }} />}
                  label="Bestseller"
                  size="small"
                  sx={{
                    position: 'absolute',
                    left: 12,
                    top: 12,
                    zIndex: 1,
                    bgcolor: '#FFD700',
                    color: '#000',
                    fontWeight: 600,
                  }}
                />
              )}

              <IconButton
                onClick={() => removeFromFavorites(product.id)}
                sx={{
                  position: 'absolute',
                  right: 8,
                  top: 8,
                  bgcolor: 'white',
                  zIndex: 1,
                  '&:hover': {
                    bgcolor: 'error.light',
                    color: 'white',
                  },
                }}
              >
                <Delete />
              </IconButton>

              <CardMedia
                component={RouterLink}
                to={`/product/${product.id}`}
                sx={{
                  pt: '100%',
                  position: 'relative',
                  '&:hover img': {
                    transform: 'scale(1.05)',
                  },
                }}
              >
                <img
                  src={product.image}
                  alt={product.name}
                  style={{
                    position: 'absolute',
                    top: 0,
                    left: 0,
                    width: '100%',
                    height: '100%',
                    objectFit: 'cover',
                    transition: 'transform 0.3s ease',
                  }}
                />
              </CardMedia>

              <CardContent sx={{ flexGrow: 1 }}>
                <Typography
                  component={RouterLink}
                  to={`/product/${product.id}`}
                  variant="h6"
                  sx={{
                    textDecoration: 'none',
                    color: 'text.primary',
                    '&:hover': { color: 'primary.main' },
                  }}
                >
                  {product.name}
                </Typography>

                <Box sx={{ display: 'flex', alignItems: 'center', my: 1 }}>
                  <Rating value={product.rating} precision={0.1} readOnly size="small" />
                  <Typography variant="body2" color="text.secondary" sx={{ ml: 1 }}>
                    ({product.reviews})
                  </Typography>
                </Box>

                <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 1 }}>
                  <LocalShipping sx={{ color: 'text.secondary', fontSize: 20 }} />
                  <Typography variant="body2" color="text.secondary">
                    {product.shipping}
                  </Typography>
                </Box>

                <Typography variant="h6" color="primary">
                  ${product.price}
                </Typography>
              </CardContent>

              <CardActions sx={{ p: 2, pt: 0 }}>
                <Button
                  fullWidth
                  variant="contained"
                  color="primary"
                  startIcon={<ShoppingCart />}
                  disabled={!product.inStock}
                  sx={{
                    height: isMobile ? 40 : 48,
                  }}
                >
                  {product.inStock ? 'Add to Cart' : 'Out of Stock'}
                </Button>
              </CardActions>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Container>
  );
};

export default Favorites;
